import React, { useState } from "react";
import { Link } from "react-router-dom";
import { v4 as uuid } from "uuid";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import FavoriteIcon from "@material-ui/icons/Favorite";
import ShareIcon from "@material-ui/icons/Share";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Chip from "@material-ui/core/Chip";
import Tooltip from '@material-ui/core/Tooltip';


const useStyles = makeStyles((theme) => ({
  root: {
    width:'15vw',
    minWidth: 140,
    margin: "1rem",
    backgroundColor: "#ffffff",
  },
  Card: {
    width:'30%'
  },
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  }
}));

export default function SmallCard({ recipe }) {
  const classes = useStyles();
  const [expanded, setExpanded] = useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  let ingredientIndex = 1;
  let ingredients = [];
  // console.log(recipe);

  while (recipe["strIngredient" + ingredientIndex]) {
    ingredients.push(recipe["strIngredient" + ingredientIndex]);
    ingredientIndex += 1;
  }
  const ingredientLinks = ingredients.slice(0, 6).map((ingredient) => {
    return (
      <Chip
        key={uuid()}
        color="secondary"
        style={{ margin: "3px" }}
        label={
          <Link
            key={uuid()}
            style={{ color: "inherit", textDecoration: "none" }}
            to={`/ingredients/${ingredient}`}
          >
            {ingredient}
          </Link>
        }
      />
    );
  });
  // console.log(ingredientLinks);

  return (
    <Card className={classes.root} style={{display:'flex', flexDirection:'column'}}>
      <CardHeader
        // className={classes.CardHeader}
        titleTypographyProps={{variant:'body1'}}
        // avatar={
        //   <Avatar aria-label="recipe" className={classes.avatar}>
        //     R
        //   </Avatar>
        // }
        // action={
        //   <IconButton aria-label="settings">
        //     <MoreVertIcon />
        //   </IconButton>
        // }
        title={
          <Tooltip title={recipe.strMeal}>
            <Link
              key={uuid()}
              style={{ color: "inherit", textDecoration: "none" }}
              to={`/recipes/${recipe.idMeal}`}
            >
              {recipe.strMeal}
            </Link>
          </Tooltip>
        }
        // subheader={
        //   <div>
        //     <Link
        //       key={uuid()}
        //       style={{ color: "inherit" }}
        //       to={`/ingredients/${recipe.strCategory}`}
        //     >
        //       {recipe.strCategory}
        //     </Link>
        //     {` - `}
        //     <Link
        //       key={uuid()}
        //       style={{ color: "inherit" }}
        //       to={`/ingredients/${recipe.strArea}`}
        //     >
        //       {recipe.strArea}
        //     </Link>
        //   </div>
        // }
      />
      <CardMedia
        className={classes.media}
        image={recipe.strMealThumb}
        title={recipe.strMeal}
      />
      {/* <CardContent>
        {ingredientLinks}
      </CardContent> */}
      <CardActions disableSpacing style={{marginTop:'auto'}}>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
}
